.skillfulai__navbar {
   display: flex;
   justify-content: space-between;
   align-items:  center;

   padding: 0.01rem 3rem;
  }

.skillfulai__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items:  center;
 
    padding: 1rem 6rem;
   }
   
  
.skillfulai__navbar-links_logo{
    margin-right: 2rem ;
  }
  
.skillfulai__navbar-links_logo img {
    width: 140px;
    height: 120px;
  }  

.skillfulai__navbar-links_container {
    display: flex;
}

.skillfulai__navbar-sign img {
    display: flex;
    justify-content: flex-end;
    align-items:  center;
  }

.skillfulai__navbar-links_container p, 
.skillfulai__navbar-sign p, 
.skillfulai__navbar-links_menu p {
    color: #fff ;
    font-family:   var(--font-family); 
    font-weight: 500;
    font-size:  18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
  }

.skillfulai__navbar-sign button,
.skillfulai__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border:  0;
    outline: none;
    cursor: pointer;

}

.skillfulai__navbar-sign {
    display: flex;
    align-items: center;
    justify-content: space-between; /* adjust as needed */
    width: 200px; /* adjust as needed */
}

.skillfulai__navbar-menu{
    margin-left: 1rem;

    display: none;
    position: relative;
}

.skillfulai__navbar-menu svg {
    cursor: pointer;
}

.skillfulai__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2)

}

.skillfulai__navbar-menu_container p{
    margin: 1rem 0;

}

.skillfulai__navbar-menu-container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px){
    .skillfulai__navbar-links_container {
        
         display: none; 
    }
    .skillfulai__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .skillfulai__navbar {
        padding: 2rem 4rem;
    }
}


@media screen and (max-width: 550px){
    .skillfulai__navbar {
        padding: 2rem;
    }

    .skillfulai__navbar-sign{
        display: none;
    }

    .skillfulai__navbar-menu_container{
        top: 20px
    }

    .skillfulai__navbar-menu-container-links-sign{
        display: block;

    }
}